import { Directive, ElementRef, OnInit, Input, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Subscription } from 'rxjs';
import { IResource } from '../models/resource';
import { ResourceService } from '../services/resource.service';
import { iconPath } from '../themes/icons';

@Directive({
  // tslint:disable-next-line: directive-selector
  selector: '[icon]'
})
export class IconDirective implements OnInit, OnDestroy {
    private subscription: Subscription;

    @Input('icon')
    resource: IResource;

    constructor(
        private readonly el: ElementRef,
        private readonly resources: ResourceService,
        private readonly changes: ChangeDetectorRef,
    ) {
    }

    ngOnInit(): void {
        this.el.nativeElement.classList.add('resource-icon');
        this.subscription = this.resources.focused.subscribe(focus => {
            if (focus && this.resource && focus.path === this.resource.path) {
                this.resource = focus;
                this.refresh();
            }
        });
        this.refresh();
    }

    ngOnDestroy(): void {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }

    async refresh(): Promise<void> {
        const native = this.el.nativeElement as HTMLImageElement;
        native.src = await iconPath(this.resource);
        native.style.width = '16px';
        native.style.height = '16px';
        native.style.marginRight = '4px';
    }

}
