import {Component, Input, ContentChild, TemplateRef, OnInit } from '@angular/core';
import { FlatTreeControl } from '@angular/cdk/tree';
import { MatTreeFlattener, MatTreeFlatDataSource } from '@angular/material/tree';
import { TreeNodeDirective } from '../../directives/tree-node.directive';


/** Representation of a tree node */
export interface Node {
    /** unique id of the ndoe */
    id?: string;
    /** name of the node */
    name: string;
    /** level of the node */
    level?: number;
    /** is the node expandable? */
    expandable?: boolean;

    /**
     * An array of the node's children.
     * Each child is an object with the same structure as the parent node.
     */
    children: Node[];

    /** data of the node */
    data?: any;
    /** is the node focused */
    focused?: boolean;
    /** is the node expanded */
    expanded?: boolean;
    /** is the node in a create state */
    creating?: boolean;
    /** is the node in a rename state */
    renaming?: boolean;
}

export interface NodeMouseEvent<T extends Node> {
    node: T;
    event: any;
}

export interface NodeEditEvent<T extends Node> {
    node: T;
    editedText: string;
    event: any;
}

export interface TreeOptions<T extends Node> {
    /**
     * A string representing the attribute of the node that contains the unique ID.
     * This will be used to construct the path, which is an array of IDs that point to the node.
     *
     * If not provided the component will throw an exception.
     */
    idField: string;
    edited?: (e: NodeEditEvent<T>) => void;
    selected?: (e: NodeMouseEvent<T>) => void;
    expanded?: (e: Node) => void;
    contextmenu?: (e: NodeMouseEvent<T>) => void;
}

/** Renders a recursive tree */
@Component({
  selector: 'app-tree',
  templateUrl: 'tree.component.html',
  styleUrls: ['tree.component.scss'],
})
export class TreeComponent<T extends Node> implements OnInit {
    private editingParent: T;
    private _nodes: T[];

    editedName: string;

    /** a reference to the focused node */
    focused: T;

    /** a reference to the node currenlty in a editing state */
    editing: T;

    treeControl: FlatTreeControl<T>;
    treeFlattener: MatTreeFlattener<T, T>;
    dataSource: MatTreeFlatDataSource<T, T>;

    /**
     * TemplateRef that renders a node (a default renderer will be used if not provided)
     */
    @ContentChild(TreeNodeDirective, { read: TemplateRef })
    template: any;

    @Input()
    options: TreeOptions<T> = {
        idField: ''
    };

    @Input()
    set nodes(nodes: T[]) {
        this._nodes = nodes;
        this.build();
    }

    constructor() {}

    ngOnInit(): void {
        this.build();
    }

    /**
     * Checks if the `node`is expandable
     */
    expandable(_: number, node: Node): boolean {
        return node.expanded || node.expandable;
    }

    /**
     * Expands the passed node and recursively expand all its parents.
     */
    expand(e: string | T) {
        const node = this.findNode(e);
        if (node) {
            if (this.options.expanded) {
                this.options.expanded(node);
            }
            const parent = this.findParent(node);
            this.treeControl.expand(parent);

            if (parent) {
                parent.expanded = true;
                if (this.options.expanded) {
                    this.options.expanded(parent);
                }
            }

            if (parent && parent.level > 0) {
                this.expand(parent);
            }
        }
    }

    collapse(e: string | T) {
        const node = this.findNode(e);
        if (node) {
            this.treeControl.collapse(node);
        }
    }

    /**
     * Adds `node` to the children of `parent`
     * and sets the parent in a editing state.
     * @param parent the parent of the node to add.
     * @param node the node to add.
     */
    pushNode(parent: T, node: T) {
        parent.creating = true;
        parent.renaming = false;
        parent.expanded = true;

        this.editedName = '';
        this.editing = this.transformer(node);
        this.editing.creating = true;
        this.editing.renaming = false;

        this.editingParent = parent;
        this.treeControl.expand(parent);
    }

    renameNode(node: T) {
        node.renaming = true;
        this.editedName = node.name;

        this.editing = node;
        this.editing.creating = false;
        this.editing.renaming = true;
    }

    endEdit() {
        if (this.editingParent) {
            this.editingParent.creating = this.editingParent.renaming = false;
        }
        this.editing.creating = this.editing.renaming = false;
        this.editing = this.editingParent = null;
        // TODO fix throw exception after renaming
    }

    /**
     * Emits `editing` event with the original
     * KeyboardEvent `event`
     * @param event the original event.
     */
    onEdit(event: any) {
        if (this.options.edited) {
            this.options.edited({
                node: this.editing,
                event,
                editedText: this.editedName,
            });
        }
    }

    onTrack(_: number, item: T) {
        return item.id;
    }

    /**
     * Focus the node `e` inside the tree, expand all its parents
     * and moves the scroller to make the node visible.
     * @param e A node id or a reference to a node.
     */
    onFocus(e: string | T) {
        if (this.focused) {
            this.focused.focused = false;
            this.focused = null;
        }

        this.focused = this.findNode(e);
        if (this.focused) {
            this.focused.focused = true;
            this.focused.expanded = this.focused.expandable && !this.focused.expanded;

            this.expand(this.focused);

            const node = document.getElementById('tree-node-' + this.focused.id);
            if (node) {
                node.focus();
            }
        }
    }

    /**
     * Focus the `node` and emits `clicked` event.
     * @param node the node.
     * @param event original event.
     */
    onClick(node: T, event: MouseEvent) {
        if (this.options.selected) {
            this.options.selected({ node, event });
        }
    }

    onContextMenu(node: T, event: MouseEvent) {
        if (this.options.contextmenu) {
            this.options.contextmenu({ node, event });
        }
    }

    build() {
        this.treeControl = new FlatTreeControl<T>(
            node => node.level,
            node => node.expandable
        );

        this.treeFlattener = new MatTreeFlattener<T, T>(
            (node, level) => this.transformer(node, level),
            node => node.level,
            node => node.expandable,
            node => node.children as T[]
        );

        this.dataSource = new MatTreeFlatDataSource(
            this.treeControl,
            this.treeFlattener
        );

        this.connect();
    }

    private findNode(e: string | T): T {
        if (e && typeof(e) ===  'string') {
            return this.treeControl.dataNodes.find(n => n.id === e);
        }
        return e as T;
    }

    /**
    * Iterates over each node in reverse order and return the first node
    * that has a lower level than the passed node.
    */
    private findParent(node: T) {
        const currentLevel = this.treeControl.getLevel(node);
        if (currentLevel < 1) {
            return null;
        }
        const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

        for (let i = startIndex; i >= 0; i--) {
            const currentNode = this.treeControl.dataNodes[i];
            if (this.treeControl.getLevel(currentNode) < currentLevel) {
                return currentNode;
            }
        }
    }

    private connect() {
        if (this.options.idField && this.dataSource) {
            this.dataSource.data = this._nodes;
            this.treeControl.dataNodes.forEach(node => {
                if (node.expanded) {
                    this.treeControl.expand(node);
                }
            });
        }
    }

    private transformer(node: T, level?: number): T {
        node.level = level;
        node.expandable = !!node.children && node.children.length > 0;
        if (!this.options.idField) {
            throw new Error('@Input() idField must be provided');
        }
        if (!(this.options.idField in node)) {
            throw new Error(`property '${this.options.idField}' must be present in the nodes`);
        }
        node.id = node[this.options.idField];
        return node;
    }

}

