import { LanguageDefinition } from './language';

// tslint:disable: max-line-length

/**
 * Provides syntax highlighter for pl and pltp files inside monaco editor.
 */
export class PremierLangage implements LanguageDefinition {

    /** Regex expression that matches files referenced inside pl and pltp files */
    private static readonly REFERENCE_PATTERN = /(@|(?:\$=)|(?:template|grader|builder|extends|builder|grader)\s*=)\s*(\w+:)?([~a-zA-Z0-9_\.\/-]+)/;

    // Modify the rule multiline inside the method registerMonarch if this is modified !!!
    /** Regex expression that matches the start of multiline keys inside pl and pltp files */
    private static readonly MULTI_LINE_PATTERN = /^[a-zA-Z_](\.?\w+)*\s*((==)|(%=)|((\+|-)=(?!@)))/;

    /** Regex expression that matches the end of multiline keys inside pl and pltp files */
    private static readonly END_MULTI_LINE_PATTERN = /^==\s*$/;

    /** Built-in keys of premier langage */
    private static readonly BUILT_IN = {
        title: 'Titre de l\'exercice/feuille d\'exercice',
        author: 'Auteur de l\'exercice',
        introduction: 'Présentation de la feuille d\'exercice, le contenu de cette clé est interprété comme du markdown.',
        teacher: 'Sur un PLTP, affiche un note visible par les enseignant seulement',
        text: 'Énoncé de l\'exercice, le contenu de cette clé est interprété comme du markdown.',
        build: 'Clé contenant une fonction build (ancienne syntaxe: utiliser de préférence before), à utiliser avec le builder /builder/build.py',
        before: 'Code python permettant de modifier l\'exercice avant son exécution sur le navigateur',
        form: 'Formulaire HTML permettant à l\'élève de répondre',
        template: 'Définie template comme étant la base de ce fichier',
        extracss: 'Ajoute des feuilles de styles supplémentaires à la page HTML',
        extrajs: 'Ajoute des scripts supplémentaires à la page HTML'
    };

    private static readonly COMPONENTS = {
        'c-automaton-editor': {
snippet:
`
component % { "cid": "component", "selector": "pl-automaton-editor" }
`,
        },
        'c-automaton-drawer': {
snippet:
`
component % { "cid": "component", "selector": "pl-automaton-drawer" }
component.automaton ==
#states
s0
s1
#initial
s0
#accepting
s1
#alphabet
a
b
#transitions
s0:a>s1
s1:b>s1
==
`,
        },
        'c-checkbox-group': {
snippet:
`
component % { "cid": "component", "selector": "pl-checkbox-group" }
component.items %=
[
    { "id": "A", "content": "A" },
    { "id": "B", "content": "B" }
]
==
`,
        },
        'c-code-editor': {
snippet:
`
component % { "cid": "component", "selector": "pl-code-editor" }
component.language = python
component.code ==
TYPE YOUR CODE HERE
==
`,
        },
        'c-drag-drop': {
snippet:
`
drag % { "cid": "drag", "selector": "pl-drag-drop" }
drag.content = Drag Me

drop % { "cid": "drop", "selector": "pl-drag-drop" }
drop.droppable % true
`,
        },
        'c-input': {
snippet:
`
component % { "cid": "component", "selector": "pl-input" }
component.type = text
`,
        },
        'c-match-list': {
snippet:
`
component % { "cid": "component", "selector": "pl-match-list" }
component.items %=
[
    { id: 'S1', content: 'S1', target: false, source: true, },
    { id: 'S2', content: 'S2', target: false, source: true, },
    { id: 'S3', content: 'S3', target: false, source: true, },

    { id: 'T1', content: 'T1', target: true, source: false, },
    { id: 'T2', content: 'T2', target: true, source: false, },
    { id: 'T3', content: 'T3', target: true, source: false, },
]
==

`,
            description: '...',
        },
        'c-math-input': {
snippet:
`
component % { "cid": "component", "selector": "pl-math-input" }
`,
        },
        'c-math-drawer': {
snippet:
`
component % { "cid": "component", "selector": "pl-math-drawer" }
`,
        },
        'c-matrix': {
snippet:
`
component % { "cid": "component", "selector": "pl-matrix" }
component.matrix %=
[
    [{ "value": 0 }, { "value": 0 }],
    [{ "value": 0 }, { "value": 0 }]
]
==
`,
        },
        'c-radio-group': {
snippet:
`
component % { "cid": "component", "selector": "pl-radio-group" }
component.items %=
[
    { "id": "R1", "content": "R1" },
    { "id": "R2", "content": "R2" }
]
==
`,
        },
        'c-sort-list': {
snippet:
`
component % { "cid": "component", "selector": "pl-sort-list" }
component.items %=
[
    { "id": "A", "content": "A" },
    { "id": "B", "content": "B" }
]
==
`,
        },
        'c-text': {
snippet:
`
component % { "cid": "component", "selector": "pl-text" }
component.text ==
==
component.selectable = false
`,
        },
    };

    readonly id = 'pl';
    readonly extensions =  ['.pl', '.pltp'];

    /**
     * Registers syntax highlighter for pl language inside monaco editor using
     * the monarch api described at :
     * https://microsoft.github.io/monaco-editor/monarch.html
     */
    syntax() {
        return {
            // Set defaultToken to invalid to see what you do not tokenize yet
            // defaultToken: 'invalid',
            keywords: [
                'title', 'author', 'introduction', 'teacher', 'text', 'build', 'before', 'form', 'template',
                'extracss', 'extrajs', 'evaluator', 'extends'
            ],
            tokenizer: {
                root: [
                    // KEYS
                    [
                        /^[a-zA-Z_](\.?\w+)*/, {
                            cases: {
                                '@keywords': 'keyword',
                                '@default': 'keyword'
                            }
                        }
                    ],

                    // COMMENT
                    [/#.*/, 'comment'],

                    // SANDBOX FILE
                    [/@.+/, 'text'],

                    // MULTI LINE OPERATORS
                    [/(==)|(%=)|((\+|-)=(?!@))/, { token: 'operator', bracket: '@open', next: '@multiline' }],

                    // ONE LINE OPERATORS
                    [/((=@)|(\+=@)|(-=@)|(\$=)|=|\+|\-|\%)/, { token: 'operator', next: '@oneline' }],

                    // NUMBERS
                    [/\d*\.\d+([eE][\-+]?\d+)?/, 'number.float'],
                    [/0[xX][0-9a-fA-F]+/, 'number.hex'],
                    [/\d+/, 'number'],

                    // WHITESPACE
                    { include: '@whitespace' },
                ],


                oneline: [
                  [/.*|\n/, { token: 'text', next: '@pop' }]
                ],


                multiline: [
                    [/#\|(\w+)\|/, { token: 'string', next: '@embedded', nextEmbedded: '$1' }],
                    [/\{\{\s*[a-zA-Z_](\.?\w+)*\s*\}\}/, 'keyword'],
                    [/^==\s*$/, { token: 'operator', bracket: '@close', next: '@popall' }],
                    [/./, 'text'],
                ],


                embedded: [
                    [/^==\s*$/, { token: 'operator', bracket: '@close', next: '@popall', nextEmbedded: '@pop' }],
                ],


                whitespace: [
                    [/[ \t\r\n]+/, 'white'],
                ],
            },
        } as any;
    }

    hoversProviders(): monaco.languages.HoverProvider[] {
        return [{
            provideHover: function (model, position) {
                try {
                    const token = model.getWordAtPosition(position);
                    if (token) {
                        if (token.startColumn === 1 && token.word in PremierLangage.BUILT_IN) {
                            const lineCount = model.getLineCount();
                            return {
                                range: new monaco.Range(1, 1, 3, model.getLineMaxColumn(lineCount)),
                                contents: [
                                    { value: '**PL BUILT-IN**' },
                                    { value: PremierLangage.BUILT_IN[token.word] }
                                ]
                            };
                        }
                    }
                } catch {
                    // TODO freeze on firefox
                }
            }
        }];
    }

    linksProviders(): monaco.languages.LinkProvider[] {
        return [
            {
                provideLinks: function (model, _token) {
                    const links = [];
                    try {
                        const lines: string[] = model.getValue().split('\n');
                        const length = lines.length;
                        let i = 0;
                        let match: RegExpExecArray;
                        while (i < length) {
                            if (lines[i].match(PremierLangage.MULTI_LINE_PATTERN)) {
                                i++;
                                while (i < length) {
                                    if (lines[i].match(PremierLangage.END_MULTI_LINE_PATTERN)) {
                                        break;
                                    }
                                    i++;
                                }
                                if (i >= length) {
                                    break;
                                }
                            }

                            match = PremierLangage.REFERENCE_PATTERN.exec(lines[i]);
                            if (match) {
                                let comment = false;
                                while (match.index >= 0) {
                                    if (lines[i][match.index] === '#') {
                                        comment = true;
                                        break;
                                    }
                                    match.index--;
                                }
                                if (!comment) {
                                    const url = match.pop();
                                    const index = lines[i].indexOf(url);
                                    links.push({
                                        range: new monaco.Range(i + 1, index + 1, i + 1, index + url.length + 2),
                                        url: url,
                                    });
                                }
                            }
                            i++;
                        }
                    } catch {
                        // TODO freeze on firefox
                    }
                    return links;
                },
                resolveLink: function (link, _token) {
                    return link;
                }
            }
        ];
    }

    foldingsProviders(): monaco.languages.FoldingRangeProvider[] {
        return [
            {
                provideFoldingRanges: (model) => {
                    const ranges = [];
                    try {
                        const lines: string[] = model.getValue().split('\n');
                        const length = lines.length;
                        let i = 0, start = -1;
                        while (i < length) {
                            if (lines[i].match(PremierLangage.MULTI_LINE_PATTERN)) {
                                start = i;
                            } else if (lines[i].match(PremierLangage.END_MULTI_LINE_PATTERN)) {
                                ranges.push({
                                    start: start + 1,
                                    end: i + 1,
                                    kind: monaco.languages.FoldingRangeKind.Region
                                });
                                start = -1;
                            }
                            i++;
                        }
                    } catch {
                        // TODO freeze on firefox
                    }
                    return ranges;
                }
            }
        ];
    }

    completionsProviders(): monaco.languages.CompletionItemProvider[] {
        return [
            {
                provideCompletionItems: (model, position) => {
                    // 10 is an arbitrary number to provide completion only for line starts.
                    if (position.column > 10) {
                        return [];
                    }
                    // const line = model.getLineContent(position.lineNumber);
                    return Object.keys(PremierLangage.BUILT_IN).map(name => {
                        let embed = 'python';
                        switch (name) {
                            case 'evaluator':
                            case 'before':
                                embed = 'python';
                                break;
                            case 'title':
                            case 'text':
                            case 'form':
                            case 'extrajs':
                            case 'extracss':
                            case 'form':
                                embed = 'html';
                                break;
                        }
                        return {
                            label: name,
                            detail: PremierLangage.BUILT_IN[name],
                            insertText: `${name} == #|${embed}| \n\n==`,
                            kind: monaco.languages.CompletionItemKind.Snippet,
                        };
                    });
                },
            },
            {
                triggerCharacters: ['c-'],
                provideCompletionItems: (model, position) => {
                    // 10 is an arbitrary number to provide completion only for line starts.
                    if (position.column > 10) {
                        return [];
                    }
                    // const line = model.getLineContent(position.lineNumber);
                    return Object.keys(PremierLangage.COMPONENTS).map(name => {
                        const component = PremierLangage.COMPONENTS[name];
                        return {
                            label: name,
                            detail: '',
                            insertText: component.snippet,
                            kind: monaco.languages.CompletionItemKind.Snippet,
                        };
                    });
                },
            }
        ];
    }

}
