import { Component } from '@angular/core';

import { GitService } from '../shared/services/git.service';
import { ResourceService } from '../shared/services/resource.service';
import { NavigationService } from './navigation.service';
import { NotificationService } from 'src/app/shared/services/notification.service';
import { SettingsService } from '../shared/services/settings.service';
import { Settings } from '../shared/models/settings';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss'],
})
export class NavigationComponent {
    size = 25;
    index = 0;

    constructor(
        private readonly git: GitService,
        private readonly settings: SettingsService,
        private readonly navigation: NavigationService,
        private readonly notification: NotificationService,
    ) {}

    didTapButton(index: number) {
        if (index === this.index) {
            this.size = this.size === 25 ? 0 : 25;
        }
        this.index = index;
    }

    didTapExplorer() {
        this.didTapButton(0);
    }

    didTapSearch() {
        this.didTapButton(1);
    }

    didTapGit() {
       this.didTapButton(2);
    }

    didTapConsole() {
        this.navigation.debugging.next();
    }


    didTapComponents() {
        const url =  window.location.origin + '/components';
        window.open(url, '_blank');
    }

    didTapTheme() {
        const theme = this.settings.get(Settings.EDITOR_KEY, 'theme');
        const newTheme = theme.value === 'light-theme' ? 'dark-theme' : 'light-theme';
        this.settings.set(Settings.EDITOR_KEY, 'theme', newTheme);
    }

    didTapSettings() {
        this.navigation.settings.next();
    }

    gitBadge() {
        return this.git.size;
    }

    consoleBadge() {
        return this.notification.size;
    }

}
