import { Component, OnInit, Input } from '@angular/core';
import { ResourceService } from '../../shared/services/resource.service';
import { IResource, ResourceTypes } from '../../shared/models/resource';


@Component({
  // tslint:disable-next-line: component-selector
  selector: 'search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

    private dataSource: IResource[] = [];

    entries: IResource[] = [];
    querying: boolean;
    query = '';
    size = 0;
    empty = false;

    constructor(private resources: ResourceService) { }

    ngOnInit() {
        this.dataSource = this.resources.findAll((r => {
            return true;
        }));
    }

    search(event: KeyboardEvent) {
        // tslint:disable-next-line: deprecation
        if (event.keyCode === 13) {
            this.querying = true;
            this.query = this.query.trim().toLocaleLowerCase();
            if (this.query) {
                this.entries = this.dataSource.filter((e => {
                    const index = e.name.toLocaleLowerCase().indexOf(this.query);
                    return e.type === ResourceTypes.File && index === 0;
                }));
                this.size = this.entries.length;
                this.empty = this.size === 0;
            } else {
                this.size = 0;
                this.empty = true;
                this.entries = [];
            }
            this.querying = false;
        }
    }
}
