import { Component, HostBinding, OnInit, OnDestroy } from '@angular/core';
import { OverlayContainer } from '@angular/cdk/overlay';
import { Settings } from './editor/shared/models/settings';
import { Subscription } from 'rxjs';
import { Router, RouterEvent, NavigationStart, NavigationEnd, NavigationCancel, NavigationError } from '@angular/router';
import { SettingsService } from './editor/shared/services/settings.service';
import { Asserts } from './shared/models/assert.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit, OnDestroy {
    private readonly subscriptions: Subscription[] = [];
    private readonly header: HTMLElement;

    loading = false;

    @HostBinding('class')
    theme: string;

    constructor(
        private readonly router: Router,
        private readonly settings: SettingsService,
        private readonly overlayContainer: OverlayContainer,
    ) {
        this.header = document.querySelector('header');
        router.events.subscribe((event: RouterEvent) => {
            this.navigationInterceptor(event);
        });
    }

    ngOnInit(): void {
        this.subscriptions.push(this.settings.changed.subscribe(e => {
            this.setTheme(this.settings.get(Settings.EDITOR_KEY, 'theme').value);
        }));
        this.setTheme('dark-theme');
    }

    ngOnDestroy(): void {
        this.subscriptions.forEach(s => s.unsubscribe());
    }

    setTheme(theme: string) {
        const container = this.overlayContainer.getContainerElement();
        container.classList.remove('dark-theme');
        container.classList.remove('light-theme');
        container.classList.add(theme);
        this.theme = theme;
    }

    navigationInterceptor(event: RouterEvent): void {
        if (event instanceof NavigationStart) {
            this.header.style.display = 'none';
            document.body.classList.remove('loaded');
            this.loading = true;
        }
        if (event instanceof NavigationEnd) {
            this.endLoading();
        }

        // Set loading state to false in both of the below events to hide the spinner in case a request fails
        if (event instanceof NavigationCancel) {
            this.endLoading();
        }
        if (event instanceof NavigationError) {
            this.endLoading();
        }
    }

    private endLoading() {
        this.header.style.display = 'flex';
        document.body.classList.add('loaded');
        setTimeout(() => {
            this.loading = false;
            document.body.classList.remove('loaded');
        }, 3000);
    }

}
